body {
  background-color: rgba(0, 0, 0, .808);
  flex-direction: column;
  align-items: center;
  gap: 20px;
  display: flex;
}

.cat-info {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  display: flex;
}

.cats-info {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.cat-temperament {
  width: 600px;
}

.cat-name {
  color: #11b5b5;
}

.breed-select {
  width: 600px;
  height: 50px;
  font-size: large;
  font-style: inherit;
  color: #000;
  text-align: center;
  background-color: #808383;
  border: 2px solid #11b5b5;
  border-radius: 10px;
}

.cat-description {
  width: 600px;
}

h1, h2 {
  margin: 0;
}

.temperament-span {
  color: #5f7b7b;
  font-style: italic;
  font-weight: bold;
}

.hidden {
  display: none;
}

.loader {
  width: 108px;
  justify-content: space-between;
  display: flex;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  width: 48px;
  height: 48px;
  background-color: #fff;
  background-image: radial-gradient(14px, #0d161b 100%, rgba(0, 0, 0, 0) 0);
  background-repeat: no-repeat;
  border-radius: 50%;
  animation: eyeMove 10s infinite, blink 10s infinite;
  display: inline-block;
}

@keyframes eyeMove {
  0%, 10% {
    background-position: 0 0;
  }

  13%, 40% {
    background-position: -15px 0;
  }

  43%, 70% {
    background-position: 15px 0;
  }

  73%, 90% {
    background-position: 0 15px;
  }

  93%, 100% {
    background-position: 0 0;
  }
}

@keyframes blink {
  0%, 10%, 12%, 20%, 22%, 40%, 42%, 60%, 62%, 70%, 72%, 90%, 92%, 98%, 100% {
    height: 48px;
  }

  11%, 21%, 41%, 61%, 71%, 91%, 99% {
    height: 18px;
  }
}

